*{
    // border: 1px solid red;
    margin: 0px;
    padding: 0px;
    font-family: "Alatsi", sans-serif;
    font-weight: 400;
    font-style: normal;
    
}

html {
    scroll-behavior: smooth;
}

body{
    margin: 0;
    background-color: #0D0D0D;
}

nav{
    width: 100%;
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 9vh;
    background-color: rgba(13, 13, 13, 0.5);
    backdrop-filter: blur(10px);
    color: white;
    z-index: 2;
    

    .left{
        display: flex;
        flex-direction: column;
        margin-left: 50px;
        h3{
            font-size: 25px;
        }
    
    }

    .right{
        display: flex;
        flex-direction: row;
        margin-right: 50px;

        a{
            margin: 0px 10px;
            text-decoration: none;
            color: white;
            font-family: "Alatsi", sans-serif;
            font-size: 20px;
        }
    }

}

.presentaion{

    width: 100%;
    height: 100vh;
    background-image: url("assets/bg3.png");
    margin: 0;
    background-position: center; 
    background-repeat: no-repeat; 
    background-size: cover; 
    display: flex;
    flex-direction: column;
    justify-content: end;

    

    .me{
        position: absolute;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        img{
            height: 30vh;
            border-radius: 300px;
            
            // transform: translateZ(1);
        }

        h1{
            color: white;
            font-size: 50px;
            text-align: center;
            margin-top: 10px;
        }
    }

    .text{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 30%;
        background-color: rgba(13, 13, 13, 0.5);
        backdrop-filter: blur(10px);
        p{
            width: 40%;
            // font-size: 20px;
            font-size: 2.5vh;
            color: white;
            text-align: center;
        }
        .socials{
            margin-top: 20px;
            & > *{
                transition: all 0.1s ease-in;
                &:hover{
                    transform: scale(1.2);
                }
            }
        }
    }
}

.cardBackground{
    background-image: linear-gradient(135deg, rgba(24, 24, 24, 1) 0%,rgba(13, 13, 13, 1) 80%);
    border-radius: 26px;
    position: relative;

}

.cardBackground::before {
    $b: -2px;
    border-radius: 30px;
    content: '';
    background-image: linear-gradient(0deg, rgba(255, 255, 255, 0.25) 0%, rgba(31, 31, 31, 0.8) 24%, rgba(255, 255, 255, 0.13) 80%, rgba(31, 31, 31, 0.3) 100%);
    top: $b;
    left: $b;
    bottom: $b;
    right: $b;
    position: absolute;
    z-index:-1;
}

.about{
    
    // height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: center;
    margin-top: 10vh;
    // justify-content: center;
    color: white;

    h1{
        color: white;
        text-align: center;
        font-size: 50px;
    }

    .cards{
        width: 70%;
        // height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        // justify-content: space-between;
        flex-wrap: wrap;
    }

    .firstRow{
        width: 100%;
        // height: 100%;
        display: flex;
        flex-direction: row !important;
        // flex-wrap: wrap;
        justify-content: space-between;
        
