        }
    }

    .aboutCard{
        margin-top: 4vh;
        padding: 20px;
        width: 45%;
        // height:100% !important;
        display: flex;
        flex-direction: column;
        // justify-content: center;
        align-items: center;
        h2{
            font-size: 25px;
            text-align: center;
        }
        .places{
            display: flex;
            height: 100%;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: max-content;
            margin: 10px 0px;
        }
        .place{
            width: 100%;
            // height: 100% !important;//aici
            
            display: flex;
            flex-direction: row;
            margin: 5px 0px;
            
            .dr{
                margin-left: 10px;
                h2{
                    font-size: 22px;
                    text-align: left;
                }
                h3{
