                }
            }
        }
    }

    // .educationCard{  
    //     height: 100%;
    // }

    .skillsCard{
    
        .st{
            justify-content: start;
        }
        
        
    }

    .volunteerCard{
        width:fit-content;
        .st{
            justify-content: start;
        }
        .places{
            width: 70%;
            
        }
        
    }
}

