footer{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: center;
    margin-top: 10vh;
    height: 30vh;
    // justify-content: center;
    color: white;

    .links{
        display: flex;
        flex-direction: row;
        justify-content:center;
        align-items: center;
        
        & > *{
            margin-right: 10px;
            transition: all 0.1s ease-in;
            &:hover{
                transform: scale(1.2);
            }
        }

        :nth-last-child(1){
            margin-right: 0px;
        }
    }

    

